window.dom = {
  create(string) {
    // create:function(){} å¯ä»¥ç®€å†™ä¸º create(){}
    const container = document.createElement("template"); // templateç”¨äºåˆ›å»ºå„ç±»å…ƒç´ 
    container.innerHTML = string.trim(); // æŠŠå­—ç¬¦ä¸²ä¸¤è¾¹çš„ç©ºæ ¼å»æ‰
    return container.content.firstChild; // è¿”å›containerçš„ç¬¬ä¸€ä¸ªå„¿å­
  },
  after(node, node2) {
    node.parentNode.insertBefore(node2, node.nextSibling);
  },
  before(node, node2) {
    node.parentNode.insertBefore(node2, node);
  },
  append(parent, node) {
    parent.appendChild(node);
  },
  wrap(node, parent) {
    dom.before(node, parent);
    dom.append(parent, node);
  },
  remove(node) {
    node.parentNode.removeChild(node);
    return node;
  },
  empty(node) {
    const array = [];
    let x = node.firstChild;
    while (x) {
      array.push(dom.remove(node.firstChild));
      x = node.firstChild;
    }
    return array;
  },
  attr(node, name, value) {
    // é‡è½½
    // nameå’Œvalueè¦åŠ å¼•å·
    if (arguments.length === 3) {
      node.setAttribute(name, value); // è®¾ç½®å±æ€§å€¼
    } else if (arguments.length === 2) {
      return node.getAttribute(name); // è¯»å–å±æ€§å€¼
    }
  },
  text(node, string) {
    // é€‚é…
    // stringè¦åŠ å¼•å·
    if (arguments.length === 2) {
      if ("innerText" in node) {
        node.innerText = string;
      } else {
        node.textContent = string;
      }
    } else if (arguments.length === 1) {
      if ("innerText" in node) {
        return node.innerText;
      } else {
        return node.textContent;
      }
    }
  },
  html(node, string) {
    if (arguments.length === 2) {
      node.innerHTML = string;
    } else if (arguments.length === 1) {
      return node.innerHTML;
    }
  },
  style(node, name, value) {
    // ä¸‰ç§å½¢å¼
    if (arguments.length === 3) {
      // dom.style(div, 'color', 'red')
      node.style[name] = value;
    } else if (arguments.length === 2) {
      if (typeof name === "string") {
        // dom.style(div, 'color')
        return node.style[name];
      } else if (name instanceof Object) {
        // dom.style(div, {color: 'red'})
        const object = name;
        for (let key in object) {
          node.style[key] = object[key];
        }
      }
    }
  },
  class: {
    add(node, className) {
      node.classList.add(className);
    },
    remove(node, className) {
      node.classList.remove(className);
    },
    has(node, className) {
      return node.classList.contains(className);
    },
  },
  on(node, eventName, fn) {
    node.addEventListener(eventName, fn);
  },
  off(node, eventName, fn) {
    node.removeEventListener(eventName, fn);
  },
  find(selector, scope) {
    // è®°å¾—åŠ ä¸Šç´¢å¼•ï¼Œä¾‹å¦‚ï¼Œfind('#test')[0]
    return (scope || document).querySelectorAll(selector);
    // å¦‚æœæœ‰scopeå°±è°ƒç”¨scope.querySelectorAll,å¦‚æœæ²¡æœ‰å°±è°ƒç”¨document.querySelectorAll
    // ä¾‹å¦‚åœ¨test2é‡Œæ‰¾class=redçš„å…ƒç´ ï¼Œfind('.red', test2)[0])
  },
  parent(node) {
    return node.parentNode;
  },
  children(node) {
    return node.children;
  },
  siblings(node) {
    return Array.from(node.parentNode.children).filter((n) => n !== node); // æ’é™¤æ‰node
  },
  next(node) {
    let x = node.nextSibling;
    while (x && x.nodeType === 3) {
      // å¦‚æœä¸‹ä¸€ä¸ªèŠ‚ç‚¹ä¸å­˜åœ¨ï¼Œè¿”å›xï¼›å¦‚æœå­˜åœ¨ï¼Œä¸”æ˜¯æ–‡æœ¬èŠ‚ç‚¹ï¼Œå°±å¾€ä¸‹ä¸€ä¸ªèŠ‚ç‚¹æ‰¾ï¼Œç›´åˆ°æ‰¾åˆ°ä¸‹ä¸€ä¸ªèŠ‚ç‚¹ä¸ºæ­¢
      x = x.nextSibling;
    }
    return x;
  },
  previous(node) {
    let x = node.previousSibling;
    while (x && x.nodeType === 3) {
      x = x.previousSibling;
    }
    return x;
  },
  each(nodeList, fn) {
    for (let i = 0; i < nodeList.length; i++) {
      fn.call(null, nodeList[i]);
    }
  },
  index(node){
    const list = dom.children(node.parentNode)
    let i
    for(i=0;i<list.length;i++){
      if(list[i] === node){
        break
      }
    }
    return i
  }
};
